import { db, Timestamp, FieldValue, COLLECTIONS } from '@/lib/firebase';
import { Invoice } from '@/types/models';
import { generateInvoiceReference } from '@/utils/helpers';
import { NotFoundError } from '@/utils/error';
import { logDbOperation, logInfo } from '@/utils/logger';

/**
 * Create a new invoice
 */
export async function createInvoice(data: {
  freelancerId: string;
  clientName: string;
  clientEmail: string;
  amount: number;
  dueDate: Date;
  description?: string;
  paymentMethods: ('bank_transfer' | 'card')[];
}): Promise<Invoice> {
  const startTime = Date.now();

  const invoice: Invoice = {
    invoiceId: '', // Will be set by Firestore
    reference: generateInvoiceReference(),
    freelancerId: data.freelancerId,
    clientName: data.clientName,
    clientEmail: data.clientEmail,
    amount: data.amount,
    currency: 'GBP',
    description: data.description,
    invoiceDate: Timestamp.now(),
    dueDate: Timestamp.fromDate(data.dueDate),
    status: 'draft',
    paymentMethods: data.paymentMethods,
    collectionsEnabled: false,
    collectionsAttempts: 0,
    dualConfirmationRequired: true,
    createdAt: Timestamp.now(),
    updatedAt: Timestamp.now(),
  };

  const docRef = await db.collection(COLLECTIONS.INVOICES).add(invoice);
  invoice.invoiceId = docRef.id;

  // Update the document with the ID
  await docRef.update({ invoiceId: docRef.id });

  logDbOperation('create', COLLECTIONS.INVOICES, docRef.id, Date.now() - startTime);

  return invoice;
}

/**
 * Get invoice by ID
 */
export async function getInvoiceById(invoiceId: string, userId: string): Promise<Invoice> {
  const startTime = Date.now();

  const doc = await db.collection(COLLECTIONS.INVOICES).doc(invoiceId).get();

  if (!doc.exists) {
    throw new NotFoundError('Invoice not found');
  }

  const invoice = doc.data() as Invoice;

  // Verify ownership
  if (invoice.freelancerId !== userId) {
    throw new NotFoundError('Invoice not found');
  }

  logDbOperation('read', COLLECTIONS.INVOICES, invoiceId, Date.now() - startTime);

  return invoice;
}

/**
 * List invoices with pagination and filters
 */
export async function listInvoices(params: {
  userId: string;
  status?: string;
  limit?: number;
  offset?: number;
}): Promise<{
  invoices: Invoice[];
  hasMore: boolean;
  total: number;
}> {
  const startTime = Date.now();
  const limit = params.limit || 10;
  const offset = params.offset || 0;

  let query = db
    .collection(COLLECTIONS.INVOICES)
    .where('freelancerId', '==', params.userId);

  if (params.status) {
    query = query.where('status', '==', params.status);
  }

  const snapshot = await query
    .orderBy('createdAt', 'desc')
    .limit(limit + 1)
    .offset(offset)
    .get();

  const invoices = snapshot.docs.slice(0, limit).map((doc) => doc.data() as Invoice);
  const hasMore = snapshot.docs.length > limit;

  logDbOperation('list', COLLECTIONS.INVOICES, undefined, Date.now() - startTime);

  return {
    invoices,
    hasMore,
    total: snapshot.size,
  };
}

/**
 * Update invoice
 */
export async function updateInvoice(
  invoiceId: string,
  userId: string,
  updates: Partial<Omit<Invoice, 'dueDate'>> & { dueDate?: string | Timestamp }
): Promise<Invoice> {
  const startTime = Date.now();

  // Get current invoice
  const invoice = await getInvoiceById(invoiceId, userId);

  // Only allow updates if status is 'draft'
  if (invoice.status !== 'draft') {
    throw new Error('Can only update draft invoices');
  }

  // Prepare updates with date conversion
  const updateData: any = {
    ...updates,
    updatedAt: Timestamp.now(),
  };

  // Convert dueDate if provided as string
  if (updates.dueDate && typeof updates.dueDate === 'string') {
    updateData.dueDate = Timestamp.fromDate(new Date(updates.dueDate));
  }

  // Update in Firestore
  await db.collection(COLLECTIONS.INVOICES).doc(invoiceId).update(updateData);

  logDbOperation('update', COLLECTIONS.INVOICES, invoiceId, Date.now() - startTime);

  // Return updated invoice
  return await getInvoiceById(invoiceId, userId);
}

/**
 * Delete invoice (soft delete - mark as cancelled)
 */
export async function deleteInvoice(invoiceId: string, userId: string): Promise<void> {
  const startTime = Date.now();

  // Get current invoice
  const invoice = await getInvoiceById(invoiceId, userId);

  // Only allow deletion if status is 'draft'
  if (invoice.status !== 'draft') {
    throw new Error('Can only delete draft invoices');
  }

  // Soft delete - mark as cancelled
  await db.collection(COLLECTIONS.INVOICES).doc(invoiceId).update({
    status: 'cancelled',
    updatedAt: Timestamp.now(),
  });

  logDbOperation('delete', COLLECTIONS.INVOICES, invoiceId, Date.now() - startTime);
}

/**
 * Get invoice statistics for a user
 */
export async function getInvoiceStats(userId: string): Promise<{
  totalInvoiced: number;
  totalCollected: number;
  pendingAmount: number;
  overdueAmount: number;
}> {
  const snapshot = await db
    .collection(COLLECTIONS.INVOICES)
    .where('freelancerId', '==', userId)
    .get();

  const invoices = snapshot.docs.map((doc) => doc.data() as Invoice);

  const stats = {
    totalInvoiced: 0,
    totalCollected: 0,
    pendingAmount: 0,
    overdueAmount: 0,
  };

  invoices.forEach((invoice) => {
    stats.totalInvoiced += invoice.amount;

    if (invoice.status === 'paid') {
      stats.totalCollected += invoice.amount;
    } else if (invoice.status === 'sent') {
      stats.pendingAmount += invoice.amount;
    } else if (invoice.status === 'overdue' || invoice.status === 'in_collections') {
      stats.overdueAmount += invoice.amount;
    }
  });

  return stats;
}

/**
 * Check if invoice is overdue and update status
 */
export async function checkAndUpdateOverdueInvoices(): Promise<void> {
  const now = Timestamp.now();

  const snapshot = await db
    .collection(COLLECTIONS.INVOICES)
    .where('status', '==', 'sent')
    .get();

  const batch = db.batch();
  let updatedCount = 0;

  snapshot.docs.forEach((doc) => {
    const invoice = doc.data() as Invoice;

    // Check if past due date
    if (invoice.dueDate.toDate() < now.toDate()) {
      batch.update(doc.ref, {
        status: 'overdue',
        updatedAt: now,
      });
      updatedCount++;
    }
  });

  if (updatedCount > 0) {
    await batch.commit();
    logInfo('Updated invoices to overdue status', { updatedCount });
  }
}
